import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew, BtnUpload } from 'components/common/button';
import { ColEditDelete, CartHeadAction } from 'components/common';
import { Card, Row, Col, Table, Button, message } from 'antd';
import * as _ from 'lodash';
import { Link } from 'react-router-dom';
import routeConstant from 'routes/route/routeConstant';

import { getStaffWithStructure, getStaffs, deleteStaff } from 'services/teacher';
import SearchBar from './SearchBar';
import PopupImportFile from './PopupImportFile';
import './style.scss';

const ROUTER_FORM_DATA =
  routeConstant.DANH_MUC__CHUONG_TRINH_DAO_TAO__DANH_SACH_GIAO_VIEN__NHAP_DU_LIEU;

class ListTeacherPage extends Component {
  state = {
    treeData: [],
    expandedRowKeys: {},
    currentLoadingRowKey: false,
    loading: false,
    importPopupVisible: false,
    searchParams: {},
  };

  componentDidMount() {
    this.handleSearch({});
  }

  getActiveExpandedKeys = () => {
    const { expandedRowKeys } = this.state;
    return Object.entries(expandedRowKeys)
      .filter(keyValue => keyValue[1])
      .map(keyValue => keyValue[0]);
  };

  buildSubjectGroupItems = (facultyId, subjectGroups) =>
    _.filter(subjectGroups, subjectGroup => !!subjectGroup.subjectGroupId).map(subjectGroup => ({
      facultyId,
      subjectGroupLabel: subjectGroup.label,
      subjectGroupId: subjectGroup.subjectGroupId,
      children: [],
    }));

  handleSearch = async searchParams => {
    const treeData = [];
    if (searchParams) {
      this.setState({ loading: true });
      const result = await getStaffWithStructure(searchParams);
      if (result.data && result.data.items) {
        _.forEach(result.data.items, faculty => {
          if (faculty.facultyId !== null) {
            treeData.push({
              facultyId: faculty.facultyId,
              facultyLabel: faculty.label,
              children: this.buildSubjectGroupItems(faculty.facultyId, faculty.list),
            });
          }
        });
      }
      this.setState({ treeData, loading: false, searchParams, expandedRowKeys: {} });
    } else {
      this.handleSearch(this.state.searchParams);
    }
  };

  handleLoadData = async ({ facultyId, subjectGroupId, isForceReload }) => {
    if (facultyId && subjectGroupId) {
      const {
        expandedRowKeys,
        searchParams: { fullname, gender },
      } = this.state;
      const expandedKey = this.generateRowKey({ facultyId, subjectGroupId });
      if (isForceReload || !Object.prototype.hasOwnProperty.call(expandedRowKeys, expandedKey)) {
        try {
          this.setState({
            currentLoadingRowKey: expandedKey,
          });
          const result = await getStaffs({
            facultyId,
            subjectGroupId,
            fullname,
            gender,
            isActive: true,
          });
          if (result.data && result.data.items) {
            const treeData = _.cloneDeep(this.state.treeData);
            const faculties = _.filter(treeData, item => item.facultyId === facultyId);
            if (!_.isEmpty(faculties)) {
              const selectedFaculty = faculties[0];
              if (selectedFaculty && !_.isEmpty(selectedFaculty.children)) {
                const selectedSubjectGroup = _.filter(
                  selectedFaculty.children,
                  subjectGroup => subjectGroup.subjectGroupId === subjectGroupId
                );
                if (!_.isEmpty(selectedSubjectGroup)) {
                  selectedSubjectGroup[0].children = result.data.items;
                  this.setState({ treeData });
                }
              }
            }
          }
          this.setState({
            currentLoadingRowKey: false,
          });
        } catch (err) {
          this.setState({
            currentLoadingRowKey: false,
          });
        }
      }
      if (!isForceReload) {
        this.toggleExpandRow({ facultyId, subjectGroupId });
      }
    }
  };

  toggleExpandRow = row => {
    const { expandedRowKeys } = this.state;
    const currentExpandedState =
      expandedRowKeys[this.generateRowKey(row)] !== undefined
        ? expandedRowKeys[this.generateRowKey(row)]
        : false;
    this.setState({
      expandedRowKeys: {
        ...expandedRowKeys,
        [this.generateRowKey(row)]: !currentExpandedState,
      },
    });
  };

  toggleImportPopup = importPopupVisible => {
    this.setState({ importPopupVisible });
  };

  generateRowKey = row => `${row.facultyId}_${row.subjectGroupId}_${row.staffId}`;

  handleDelete = async ({ staffId, facultyId, subjectGroupId }) => {
    await deleteStaff(staffId);
    message.success('Xa thnh cng');
    this.handleLoadData({ facultyId, subjectGroupId, isForceReload: true });
  };

  render() {
    const { currentLoadingRowKey, expandedRowKeys, loading, importPopupVisible } = this.state;
    const columns = [
      {
        title: 'Khoa',
        dataIndex: 'facultyLabel',
        key: 'facultyLabel',
        width: '200px',
      },
      {
        title: 'B mn',
        dataIndex: 'subjectGroupLabel',
        key: 'subjectGroupLabel',
        width: '200px',
        render: (text, record) =>
          text ? (
            <div>
              <Button
                className="btn-expand-collapse"
                icon={expandedRowKeys[this.generateRowKey(record)] ? 'minus-circle' : 'plus-circle'}
                onClick={() => this.handleLoadData(record)}
                loading={this.generateRowKey(record) === currentLoadingRowKey}
              />
              {text}
            </div>
          ) : (
            ''
          ),
      },
      {
        title: 'M cn b',
        dataIndex: 'staffId',
      },
      {
        title: 'H v tn',
        dataIndex: 'fullname',
      },
      {
        title: 'Gii tnh',
        dataIndex: 'genderText',
      },
      {
        title: 'Ngy sinh',
        dataIndex: 'birthdate',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        className: 'action-column',
        align: 'center',
        render: (text, record) =>
          record.staffId ? (
            <ColEditDelete
              linkEdit={{
                to: {
                  pathname: ROUTER_FORM_DATA,
                  search: `?id=${record.staffId}`,
                },
              }}
              onConfirmDelete={() => this.handleDelete(record)}
            />
          ) : (
            ''
          ),
      },
    ];
    return (
      <Card title="Danh sch gio vin">
        <Row>
          <Col span={24}>
            <CartHeadAction>
              <Link to={ROUTER_FORM_DATA}>
                <BtnAddNew />
              </Link>
              <BtnUpload onClick={() => this.toggleImportPopup(true)} />
            </CartHeadAction>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <SearchBar handleSearch={this.handleSearch} setSearchParams={this.setSearchParams} />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table
              columns={columns}
              dataSource={this.state.treeData}
              rowKey={record => `${record.facultyId}_${record.subjectGroupId}_${record.staffId}`}
              expandedRowKeys={this.getActiveExpandedKeys()}
              onExpand={(isExpanded, row) => this.toggleExpandRow(row)}
              expandIcon={({ record }) =>
                !record.subjectGroupId ? (
                  <Button
                    className="btn-expand-collapse"
                    icon={
                      expandedRowKeys[this.generateRowKey(record)] ? 'minus-circle' : 'plus-circle'
                    }
                    onClick={() => this.toggleExpandRow(record)}
                  />
                ) : (
                  ''
                )
              }
              loading={loading}
              bordered
              pagination={false}
            />
          </Col>
        </Row>
        <PopupImportFile
          visible={importPopupVisible}
          closePopup={() => this.toggleImportPopup(false)}
        />
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListTeacherPage />
  </Page>
);



