import React, { Component } from 'react';
import { Row, Col, Form, Input, Select } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import { getQueryParams } from 'utils/urlUtil';
import { SelectAjax } from 'components/common';
import { getFacultys } from 'services/faculty';
import { getSubjectGroups } from 'services/subjectGroup';

const { Option } = Select;

@Form.create({ name: 'SearchBar' })
class SearchBar extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        this.props.handleSearch(values);
      }
    });
  };

  handleReset = e => {
    this.props.form.resetFields();
    this.handleSearch(e);
  };

  render() {
    const {
      form: { getFieldDecorator },
    } = this.props;
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn H!',
          },
        ],
      },
      // facultyId: {
      //   rules: [
      //     {
      //       required: true,
      //       message: 'Vui lng chn Khoa!',
      //     },
      //   ],
      // },
      // courseId: {
      //   rules: [
      //     {
      //       required: true,
      //       message: 'Vui lng chn Kha',
      //     },
      //   ],
      // },
      // studyFieldId: {
      //   rules: [
      //     {
      //       required: true,
      //       message: 'Vui lng chn CN',
      //     },
      //   ],
      // },
    };
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={5}>
            <Form.Item label="Tn">
              {getFieldDecorator('fullname', {})(<Input placeholder="H v tn" />)}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="Gii tnh:">
              {getFieldDecorator(
                'gender',
                {}
              )(
                <Select>
                  <Option value={null}>Tt c</Option>
                  <Option value>Nam</Option>
                  <Option value={false}>N</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="Khoa">
              {getFieldDecorator(
                'facultyId',
                optionFieldDecorator.facultyId
              )(<SelectAjax service={getFacultys} />)}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="B mn">
              {getFieldDecorator('subjectGroupId', {})(<SelectAjax service={getSubjectGroups} />)}
            </Form.Item>
          </Col>
          <Col span={4}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBar;



